import { Injectable } from '@angular/core';

import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class PostsService {

  constructor(private httpClient: HttpClient) { }

   get_home()
   {
     let URL = "/apis";
     return this.httpClient.get(URL);
   }
   get_search(keyword: string)
   {
     let URL ="/apis/search/" + keyword;
     return this.httpClient.get<[any, string[]]>(URL);
   }
   get_movie_detail(id:any)
   {
    let URL = "/apis/watch/movie/" + id;
    return this.httpClient.get(URL);
   }
   get_tv_detail(id:any)
   {
    let URL = "/apis/watch/tv/" + id;
    return this.httpClient.get(URL);
   }
   get_cast_detail(id:any)
   {
    let URL = "/apis/cast/" + id;
    return this.httpClient.get(URL);
   }

}
