import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import {Observable, OperatorFunction} from 'rxjs';
import {debounceTime, map, distinctUntilChanged, switchMap, catchError } from 'rxjs/operators';
import {PostsService} from '../../services/posts.service';

@Component({
  selector: 'app-typehead',
  templateUrl: './typehead.component.html',
  styleUrls: ['./typehead.component.css']
})
export class TypeheadComponent implements OnInit {

  constructor(private PostsService: PostsService) { }
  public data: any;

  ngOnInit(): void {
  }

  // fetchData(){
  //   this.PostsService.get_search().subscribe(res => {
  //     this.data = res;
  //     console.log(res)
  //   });
  // }

  public model: any;

  search = (text$: Observable<string>) => {
      return text$.pipe(
        debounceTime(200),
        distinctUntilChanged(),
        // switchMap allows returning an observable rather than maps array
        switchMap( (searchText) =>  searchText.length < 1? [] : this.PostsService.get_search(searchText) ) );
      }

// make sure that the model is not updated and the input field is not updated with the selected item. But text entered by a user will still be part of the input so if you want to clear up this as well you can directly update the input's value property
  onSelect($event:any, input:any)
  {
    $event.preventDefault();
    input.value="";
  }

}
